.nav-title {
    font-size: 24px;
    font-family: 'Roboto', 'sans-serif';
    font-weight: 900;
    display: inline-block;
    color: black;
    padding-left: 10px;
}

.nav {
  overflow: hidden;
  background-color:#1fcecb;
}

.nav-link {
  float: left;
  color: black;
  background-color: #1fcecb;
  text-align: center;
  padding: 24px 10px;
  text-decoration: none;
  font-size: 15px;
  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-right {
  float: right;
}
footer {
    position: -webkit-sticky;
    position: sticky;
    margin-top: 95vh; 
    height: 10vh; 
    width: 100%;
}
.center-footer{
    margin-top: 10vh; 
    margin-left: 5vw;
    display: flex;
    flex-direction: row; 
}
.card{
    /* Add shadows to create the "card" effect */
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    -webkit-transition: 0.3s;
    transition: 0.3s;
    width: 50vw; 
    padding: 2.5vw;
    background-color: white; 

}
/* On mouse-over, add a deeper shadow */
.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

body {
    font-family: 'Roboto', sans-serif;
    margin: 0px;
    /* background-color:#FAF2E6;  */
    background-color: white;
}
.body-div {
    height: 100%;
}
.add-form{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column; 
    justify-content: space-evenly;
    background-color: white; 
}
.alert {
    margin: 50px;
}
.bottom {
    margin-bottom: 1%;
}
.button-margin{
    margin-top: 2vh;
    text-align: center; 
}
.center{
    text-align: center; 
 }
.date{
    padding: 1vh; 
}
#email-input {
    margin-left: 10vw;
    margin-right: 10vw; 
}
.flex-container{
    display: flex; 
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center; 
    margin-bottom: 2vh;
}
.flex-container-header{
    display: flex; 
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center; 
}
.flex-box {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; 
    justify-content: space-evenly;
}
.flex-box-header{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; 
    justify-content: space-between; 
}
.flex-box-right{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; 
    justify-content: right; 
}
form {
    background-color: #dbd5d5;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}
.form-input {
    padding: 5px; 
    margin: 10px; 
}
input {
    margin: 5px;
    margin-bottom: 10px;
}
.login-form {
    justify-content: center;
    margin-left: 35vw;
    /* width: 50vw;  */
    margin-right: 30vw; 
    margin-top: 10vh;
}
.log-in, .log-out, .register {
  background-color: #00acb0;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #008183;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}
.manage-table {
    background-color: white; 
    margin-top: 5vh; 
    font-weight: 200; 
}
.reset-form{
    display: flex;
    flex-direction: column;
    margin-top: 5vh; 
    margin-bottom: 2vh; 
}

